import Link from "next/link";
import { Clock, Calendar, Heart, MapPin, Phone, Mail, ArrowRight } from "lucide-react";

type IconType = "clock" | "calendar" | "heart" | "map" | "phone" | "mail";

interface ServiceCardProps {
  icon: IconType;
  title: string;
  description: string;
  link?: string;
}

const iconMap = {
  clock: Clock,
  calendar: Calendar,
  heart: Heart,
  map: MapPin,
  phone: Phone,
  mail: Mail,
};

export default function ServiceCard({ icon, title, description, link }: ServiceCardProps) {
  const IconComponent = iconMap[icon];
  
  const content = (
    <div className="bg-white rounded-lg shadow-lg p-6 text-center transition-all duration-300 hover:shadow-xl hover:-translate-y-1">
      <div className="w-16 h-16 bg-[#002F57] rounded-full flex items-center justify-center mx-auto mb-4">
        <IconComponent className="w-8 h-8 text-[#6DFFE5]" />
      </div>
      <h3 className="text-xl font-bold text-[#002F57] mb-3">{title}</h3>
      <p className="text-gray-600 text-sm mb-4">{description}</p>
      {link && (
        <span className="inline-flex items-center text-[#49AE9C] font-medium">
          Más información
          <ArrowRight className="w-4 h-4 ml-1" />
        </span>
      )}
    </div>
  );

  if (link) {
    return <Link href={link}>{content}</Link>;
  }

  return content;
}
